<script>
import { message } from 'ant-design-vue';
import axios from 'axios';
var a=0;
const columns = [{
  name: 'Name',
  dataIndex: 'name',
  key: 'name',
}, {
  title: '时长',
  dataIndex: 'age',
  key: 'age',
}, {
  title: 'Address',
  dataIndex: 'address',
  key: 'address',
}, {
  title: 'Tags',
  key: 'tags',
  dataIndex: 'tags',
}, {
  title: 'Action',
  key: 'action',
}];
const data = [{
  key: '1',
  name: 'John Brown',
  age: 32,
  address: 'New York No. 1 Lake Park',
  tags: ['nice', 'developer'],
}, {
  key: '2',
  name: 'Jim Green',
  age: 42,
  address: 'London No. 1 Lake Park',
  tags: ['loser'],
}, {
  key: '3',
  name: 'Joe Black',
  age: 32,
  address: 'Sidney No. 1 Lake Park',
  tags: ['cool', 'teacher'],
}];
export default {

  name: "App",
  setup() {
    const back = () => {
      var page0 = document.getElementById("anchor-0");
      page0.style.display = "block";
      var page1 = document.getElementById("anchor-1");
      page1.style.display = "none";
      var page2 = document.getElementById("anchor-2");
      page2.style.display = "none";
    };
    const back2 = () => {
      var page0 = document.getElementById("anchor-0");
      page0.style.display = "none";
      var page1 = document.getElementById("anchor-1");
      page1.style.display = "block";
      var page2 = document.getElementById("anchor-2");
      page2.style.display = "none";
    };
    const choose = () => {
      var page1 = document.getElementById("anchor-1");
      page1.style.display = "block";

      var page0 = document.getElementById("anchor-0");
      page0.style.display = "none";

    }
    const leave = () => {
      message.info('你忍心离开吗，555');

    }
    const flash = () => {
      message.info('当前挖空数:'+a);
      gamepage(a);
    }
    const gamepage = (diffic) => {
      a=diffic;
      message.info('后端并发计算中。。。绘制可能需要一段时间');
      var url = "/api";

      var text = '123456789123456789123456789123456789123456789123456789123456789123456789123456789';
      var page2 = document.getElementById("anchor-2");
      page2.style.display = "block";
      var page1 = document.getElementById("anchor-1");
      page1.style.display = "none";




      var draw = (name, text) => {
        var canvas1 = document.getElementById(name);
        var ctx = canvas1.getContext("2d");
        var image = document.getElementById("image");
        ctx.drawImage(image, 0, 0);
        var textHeight = ctx.measureText(text).height;
        ctx.font = "40px Arial";
        var gradient=ctx.createLinearGradient(0,0,canvas1.width,0);
gradient.addColorStop("0","magenta");
gradient.addColorStop("0.5","red");
gradient.addColorStop("1.0","blue");
ctx.fillStyle=gradient;
        var count = 0;
        var i = 0, j = 0, k = 0, m = 0;
        //一行一行绘制
        for (m = 0; m < 3; m++) {
          for (j = 0; j < 3; j++) {
            for (k = 0; k < 3; k++) {

              for (i = 0; i < 3; i++) {
                if (text[count] != '0') {
                  ctx.fillText(text[count], 50 + i * 85 + 271 * k, 80 + j * 85 + 271 * m);
                }

                count++;
              }
            }
          }
        }

      }

      axios.get(url, {
        params: {
          dif: diffic
        }
      }).then(function (res) {

        console.log(res.request.responseText);

        const obj = JSON.parse(res.request.responseText);

        text = obj[0];
        draw("canvas1", obj[0]);
        draw("canvas2", obj[1]);
        draw("canvas3", obj[2]);
        draw("canvas4", obj[3]);
        draw("canvas5", obj[4]);
        draw("canvas6", obj[5]);
        draw("canvas7", obj[6]);
        draw("canvas8", obj[7]);
        draw("canvas9", obj[8]);
        message.info('绘制完成');


      }).catch(function (error) {
        console.log(error);
      });








    };


    return {

      columns,
      data, back, choose, gamepage,back2,leave,flash,
    };
  },
  components: {

  },
};
</script>

